.showBlock {
  position: absolute;
  transform: translate(-50%, -50%);
  animation-duration: 2s;
  box-shadow: 0px 0px 20px 30px var(--tg-theme-bg-color);
  background: var(--tg-theme-bg-color);
}
.showBlockOn {
  animation-name: showAnimate;
}
.stylesOutline {
  position: absolute;
  width: calc(100% + 10px);
  height: calc(100% + 10px);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  border-radius: 19px;
}
.container {
  position: relative;
  width: 100%;
  height: 100%;
}

@keyframes showAnimate {
  0%{
    width: 0;
    opacity: 0.6;
    height: 0;
  }
  100%{
    width: 500px;
    height: 500px;
    opacity: 0;
  }
}