.container {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 5px 20px;
  padding-bottom: 15px;
  gap: 20px;
  overflow: hidden;
  height: 100%;
}
.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.button {
  padding: 15px;
  font-size: 100%;
}
.continue {
  margin-top: auto;
  padding: 15px;
  margin-bottom: 0;
}
