.btn {
  position: relative;
  color: var(--tg-theme-bg-color);
  background: #d0b386;
  overflow: hidden;
  cursor: pointer;
  border: none;
  font-weight: 900;
  font-size: 100%;
  transition: 0.3s;
  text-transform: uppercase;
  border-radius: 15px;
}
.disabled {
  opacity: 0.5;
  cursor: auto;
}