.container {
  position: relative;
  width: 100vw;
  max-width: 600px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
  background: var(--tg-theme-bg-color);
}
.fon {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
}
.logoBlock {
  display: flex;
  margin: 15px auto;
  text-align: start;
  gap: 10px;
  z-index: 5;
}
.logoBlock img{
  width: 50px;
}
.logoBlock h1{
  font-family: Giovanna, sans-serif;
  font-weight: 900;
  color: var(--tg-theme-text-color);
  font-size: 90%;
}
.paginate {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
}